.post {
   width: 100%;
   border-radius: 3px;
   margin-bottom: 20px;
   background-color: rgba(189, 189, 189, 0.644);
}

.post__header {
   padding: 5px;
   background-color: rgba(53, 53, 53, 0.836);
   display: flex;
   justify-content: space-between;
   border-top-left-radius: 3px;
   border-top-right-radius: 3px;
}

.post__authorImage {
   height: 60px;
   border-radius: 5px;
}



.post__body {
   padding: 10px;
   font-size: 18px;
}